$logo-color: #fff;

.logo {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: inherit;
  text-decoration: none;

  &__img {
    margin-right: 7px;
  }

  &__title {
    color: $logo-color;
    line-height: 1;
    font-size: 14.46px;
  }
}
